        var el4 = dom.createElement("b");
        var el5 = dom.createTextNode("4.");
        dom.appendChild(el4, el5);
        var el5 = dom.createElement("u");
        var el6 = dom.createTextNode(" Keep your apartment clean enough.");
        dom.appendChild(el5, el6);
